
import Toast from 'vux/src/components/toast/index.vue'

import header from '@/components/header';
import button from '@/components/button';
import timerBtn from '@/components/timerBtn';
export default {
data() {
return {
titleContent: '我的宝宝',
phoneNumber: '',
code: '',
showFlag: true
}
},
methods: {

sendCode() {

if (this.validatorPhoneNumber(this.phoneNumber)) {



this.$refs.timerbtn.start(); //启动倒计时
this.getTost('验证码已发送，请查收');
} else {
this.getTost('手机号码的格式有误');
}
},
goVerify() {
console.log("1111");



},

getTost(toastContent){
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: toastContent
});
},

validatorPhoneNumber(phoneNumber){
return /^[0-9]{11}$/.test(phoneNumber);
}
},
components: {
Toast,
'v-header': header,
'v-button': button,
'timer-btn': timerBtn
}
}



